
.set{
    position: relative;
    width: 100%;
    height: auto;
    background-color: #f5f5f5;

	a {
		position: relative;
		display: block;

		h4 {
			display: block;
			padding: 10px 15px;
			text-decoration: none;
			color: #555;
			font-weight: 600;
			border-bottom: 1px solid #ddd;
			transition:all 0.2s linear;
		}
  
		svg {
			position: absolute;
			top: 50%;
			right: 20px;
			transform: translateY(-50%);
		}
  
		&.active {
			background-color:#3399cc;
			color: #fff;
		}
    }
    
	.content {
		background-color: #fff;
		border-bottom: 1px solid #ddd;
		display: none;

		p {
			padding: 10px 15px;
			margin: 0;
			color: #333;
		}
	}
  }

